export default {
	data() {
		return {
			isSelect: false,
			addressList: []
		};
	},
	onShow() {
		
		this.loadData();
		this.getDefaultAddress()
	},
	onLoad(e) {			
		if (e.type == 'select') {
			this.isSelect = true;
		}
	},
	methods: {
		getDefaultAddress(){				
			this.$xm.post('/Order/getDefAdr', '', (res) => {					
				uni.setStorage({
					key: 'address',
					data: res,
					success: (res) => {
								
					}
				});
			})
		},
		edit(row) {
			uni.navigateTo({
				url: "edit/edit?type=edit&id=" + row.id
			})
		},
		add() {
			uni.navigateTo({
				url: "edit/edit?type=add"
			})
		},
		loadData() {				
			this.$xm.post('/User/shipAdr','', (res) => {					
				if (res) {
					res.forEach((res) => {
						res.head = res.name.substring(0, 1);
					})
					this.addressList = res ? res : [];
				}
			})
		},

		select(row) {
			//是否需要返回地址(从订单确认页跳过来选收货地址)
			if (!this.isSelect) {
				return;
			}
			
			uni.setStorage({
				key: 'selectAddress',
				data: row,
				success() {						
					uni.navigateBack();
				}
			})
		}
	}
}
